.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;

    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 10px;
      margin: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      h1 {
        color: lightgray;
        font-size: 20px;
      }
      flex: 6;
      form {
        display: flex;
        flex-wrap: wrap;

        gap: 30px;
        justify-content: space-around;
        padding: 40px;
        .fromInput {
          width: 40%;

          label {
            display: flex;
            align-items: center;
            gap: 10px;
          }
          input {
            width: 100%;
            padding: 5px;
            border: none;
            border-bottom: 1px solid gray;
          }
          .textField {
            width: 100%;
            padding: 5px;
            border: none;
          }
        }
        button {
          width: 150px;
          padding: 10px;
          border: none;
          background-color: #1976d2 !important;
          color: white;
          font-weight: bold;
          cursor: pointer;
          margin-top: 10px;
          /* Hover Effect */
          transition: background-color 0.3s ease;
        }
        button:hover {
          background-color: #438bd3 !important;
        }
      }
    }
  }
}
